import { ConfigProvider } from './../../../providers/config-provider';
import { Component } from '@angular/core';
import { IonicPage, NavController, ViewController,Events } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Session } from '../../../services/services';
import { UserProvider } from '../../../providers/providers';
import { ExtendField } from '../../../models/userinfo';

@IonicPage()
@Component({
  selector: 'page-employee-identity',
  templateUrl: 'employee-identity.html'
})
export class EmployeeIdentityPage {
  isReadyToSave: boolean;
  form: FormGroup;
  currentUser: any;
  formView: boolean; // 是否未绑定
  ExtendFields: ExtendField[] = [] // 动态栏位

  constructor(public navCtrl: NavController, formBuilder: FormBuilder,
    public viewCtrl: ViewController,
    public session: Session,
    private events:Events,
    private userProvider: UserProvider,
    private configProvider: ConfigProvider) {

    if (this.session.user != null) {
      this.currentUser = this.session.user;
      if (this.currentUser.isEmployee){ 
        if( this.currentUser && this.currentUser.employeeInfo) {
          this.ExtendFields = this.currentUser.employeeInfo.extendFields
        }
        this.formView = false;
      } 
      else {
        this.formView = true;
      }
    }
    this.form = formBuilder.group({
      staffNumber: ['', Validators.required],
      idCard: ['', Validators.required]
    });

    // Watch the form for changes, and
    this.form.valueChanges.subscribe((v) => {
      this.isReadyToSave = this.form.valid;
    });
  }

  bindEmployee() {
    //判断工号和身份证号码
 
    //绑定员工信息
    this.userProvider.BindEmployee({
      employeeNo: this.form.value.staffNumber,
      idCardNo: this.form.value.idCard
    }).subscribe((data:any)=>{
      this.userProvider.getUserInfo().subscribe((data: any) => { 
        this.currentUser = this.session.user;
        if( this.currentUser && this.currentUser.employeeInfo) {
          this.ExtendFields = this.currentUser.employeeInfo.extendFields
        }
        this.formView = false;
        setTimeout(()=>{ 
          this.events.publish('user:permission-changed');
        },1000) 
      });  
 
      if(data.result && data.result.inCompanyDate) {
        this.configProvider.AfterNewEmployeeBind(data.result.inCompanyDate,data.result.siteCode).subscribe();
      } 
    });
  }

  /**
   * 发送员工通知
   * @param siteCode 厂区编号
   */

  done() {
    if (!this.form.valid) { return; }
    this.viewCtrl.dismiss(this.form.value);
  }

  unbindEmployee() {
    this.userProvider.UnbindEmployee().subscribe((data:any)=>{
        this.userProvider.getUserInfo(true).subscribe((data: any) => { 
          this.viewCtrl.dismiss(); 
          setTimeout(()=>{ 
            this.events.publish('user:permission-changed');
          },1000)
        });  
 
      }
    );
    //this.formView = true;
  }

}



